import React from 'react'
import { connect } from 'react-redux'
import { cx } from '../../utils'
import { toggleFavoriteFilter } from '../../store/reducers/filters'
import './index.css'

const ShowFavorites = ({ on, toggle }) =>
  <div onClick={toggle} className={cx({ favoriteFilter: true, on })}></div>

export default connect(
  ({ filters }) => ({ on: filters && filters['favorites'] }),
  dispatch => ({
    toggle: () => dispatch(toggleFavoriteFilter())
  })
)(ShowFavorites)



