import { omit } from 'lodash'
import storage from '../../utils/storage'

const favoritesStorage = storage('favorites')

function toggleFavorite (favorites={}, { symbol }) {
  if (favorites[symbol]) return omit(favorites, symbol)
  return Object.assign({}, favorites, { [symbol]: true })
}

export const SET_FAVORITES = 'SET_FAVORITES'

export function loadFavorites () {
  return favoritesStorage.get()
}

export function setFavorites (favorites={}) {
  return {
    type: SET_FAVORITES,
    payload: favorites
  }
}

export function toggleCoin ({ symbol }) {
  return function (dispatch, getState) {
    const { favorites } = getState()

    return Promise.resolve(favorites)
    .then(favorites => toggleFavorite(favorites, { symbol }))
    .then(favorites => favoritesStorage.save(favorites))
    .then(favorites => dispatch(setFavorites(favorites)))
  }
}

export default function (state={}, action) {
  switch (action.type) {
    case SET_FAVORITES: return action.payload
    default: return state
  }
}



