import { createStore, applyMiddleware } from 'redux'
import thunk from 'redux-thunk'
import reducers from './reducers'
import { loadFavorites } from './reducers/favorites'

export default (initial) =>
  loadFavorites()
  .then(favorites => favorites || {})
  .then(favorites => Object.assign({}, { favorites }, initial))
  .then((initial) => createStore(reducers, initial, applyMiddleware(thunk)))



