import React, { Component } from 'react'
import { connect } from 'react-redux'
import { toggleCoin } from '../../store/reducers/favorites'
import cx from '../../utils/cx'
import './index.css'

const Favorite = ({ coin, isFavorite, favoriteAction }) =>
<div
  className={cx({favorite: true, isFavorite })}
  onClick={(ev) => {
    ev.stopPropagation()
    favoriteAction(coin)
  }} />

export default connect(
  ({ favorites }, { coin }) => ({ isFavorite: favorites[coin.symbol] }),
  (dispatch) => ({
    favoriteAction: (coin) => dispatch(toggleCoin(coin))
  })
)(Favorite)



