import React from 'react'
import { connect } from 'react-redux'
import { cx, handleScroll } from '../../utils'
import ShowFavorites from '../ShowFavorites';
import { throttle } from 'lodash'
import './index.css'

class Input extends React.Component {

  constructor (props) {
    super(props)
    this.state = {
      open: false,
      value: '',
      position: {x: 0, y: 0}
    }
  }

  componentDidMount () {
    handleScroll(
      // throttle(
        // (position) => this.setState({ position })
      // , 1000)
    )
  }

  open () {
    this.setState({ open: true })
    setTimeout(() => this.target.focus(), 400)
    this.target.focus()
  }

  close () {
    this.setState({ open: false })
    setTimeout(() => this.target.blur(), 400)
    this.target.blur()
  }

  toggle () {
    const { open } = this.state
    if (open) this.close()
    else this.open()
  }

  handleChange ({ target: { value } }) {
    const { onChange=(v=>v) } = this.props
    this.setState({ value })
    onChange({ value })
  }

  clear () {
    this.handleChange({ target: { value: "" } })
    this.target.focus()
  }

  render () {
    const { value, open, position: { y } } = this.state

    return <div className={cx({ search: true, open })}
      style={{ transform: `translate3d(0,${y || 0}px,0)` }}>

      <div
      className="toggle"
      onClick={this.toggle.bind(this)} />

      <input
      placeholder="Search"
      value={value}
      ref={target => this.target = target}
      onChange={this.handleChange.bind(this)} />

      <div
      className={cx({ clear: true, hide: !value })}
      onClick={this.clear.bind(this)} />

      <ShowFavorites />

    </div>
  }
}

export default connect(
  ({ scrollPosition }) => ({ scrollPosition }),
  (dispatch) => ({})
)(Input)



