export const SET_SCROLL_POSITION = 'SET_SCROLL_POSITION'

export function setScrollPosition (x, y) {
  return {
    type: SET_SCROLL_POSITION,
    payload: { x, y }
  }
}

export default function scrollPosition (state={x:0, y:0}, action) {
  switch (action.type) {
    case "SET_SCROLL_POSITION": return action.payload
    default: return state
  }
}



