export default function (name) {

  function get () {
    return Promise.resolve()
    .then(() => localStorage.getItem(name))
    .then((data) => JSON.parse(data))
    .catch(() => null)
  }

  function save (data) {
    return Promise.resolve()
    .then(() => JSON.stringify(data))
    .then((str) => localStorage.setItem(name, str))
    .then(() => data)
  }

  return { get, save }
}



